.item-container {
   display: flex;
   justify-content: space-between;
   width: 100%;
   padding: 1rem 1rem;
   margin: 3rem auto;
   border-radius: 10px;
   height: 100px;
   align-items: center;
   text-align: center;

   -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
   -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
   box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);

   & img {
      height: 70px;
      width: 70px;
      border-radius: 10px;
   }

   & .text-container {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: space-around;

      font-size: 1.4rem;

      & .type {
         width: 80%;
         display: flex;
         justify-content: space-around;
         align-items: center;
         font-size: 1.2rem;
         color: #e88a12;
         text-align: center;

         & i {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 30px;
            width: 30px;
            font-size: 1.8rem;
            background-color: #e88a12;
            border-radius: 1000px;
            color: white;
         }
      }

      & button {
         width: 60%;
         border-radius: 1000px;
         background-color: #e88a12;
         color: white;
         padding: 0.2rem 0;
         font-size: 1.4rem;
         letter-spacing: 1px;
      }
   }
}

@media screen and (min-width: 768px) {
   .item-container {
      height: 170px;

      & img {
         height: 90%;
         width: 20%;
      }

      & .text-container {
         font-size: 2rem;

         & .type {
            font-size: 1.6rem;

            & i {
               font-size: 3rem;
               height: 50px;
               width: 50px;
            }
         }

         & button {
            font-size: 2rem;
            width: 40%;
            transition: transform 0.2s;

            &:hover {
               cursor: pointer;
               transform: scale(1.1);
            }
         }
      }
   }
}
