.profile-section {
   height: 90vh;
   width: 90%;
   padding: 2rem 0;
   display: flex;
   flex-direction: column;
   align-items: center;
   overflow-y: auto;
   -ms-overflow-style: none;
   scrollbar-width: none;

   &::-webkit-scrollbar {
      display: none;
   }

   & .edit-profile {
      align-self: flex-end;
      color: gray;
      font-size: 1.4rem;
      display: flex;
      align-items: center;
      margin: 1rem 0 2rem;

      &:hover {
         cursor: pointer;
         text-decoration: underline;
      }
   }

   & .info-section {
      font-size: 1.6rem;
      width: 100%;
      padding: 2rem 0;
      border-top: 0.1px solid gray;

      & span {
         display: block;
         text-transform: uppercase;
         color: #e88a12;
         font-weight: bold;
      }
   }

   & button,
   a {
      width: 80%;
      max-width: 350px;
      margin: 1rem 0;
      padding: 1rem;
      font-size: 1.6rem;
      letter-spacing: 2px;
      color: white;
      border: none;
      border-radius: 1000px;
      background-color: #000000ef;
      outline: none;
      font-weight: bold;
      transition: transform 0.2s;
      text-align: center;

      &:disabled {
         background-color: #00000080;
      }

      & i {
         margin-right: 1rem;
         font-size: 2rem;
      }

      &.whatsapp {
         background-color: #25d366;
      }

      &:hover {
         transform: scale(1.1);
         cursor: pointer;

         &:disabled {
            transform: none;
            cursor: progress;
         }
      }
   }
}

@media screen and (min-width: 800px) {
   .profile-section {
      justify-content: center;

      & * {
         margin: .5rem 0;
      }

      & .info-section {
         font-size: 1.8rem;

         & span {
            font-size: 1.4rem;
         }
      }
   }
}
