.search-places-section {
   display: flex;
   flex-direction: column;
   width: 100%;
   height: 100%;
   align-items: center;

   & .search-container {
      width: 80%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 2rem 0;
      height: 50px;
      padding: 0.2rem 2rem;
      border: 1px solid rgba(128, 128, 128, 0.541);
      border-radius: 1000px;
      max-width: 500px;

      & input {
         border: none;
         outline: none;
         height: 90%;
         width: 80%;
      }

      & i {
         font-size: 2rem;
         color: #e88a12;
      }
   }

   & .icons-container {
      display: flex;
      width: 100%;
      justify-content: space-around;
      align-items: center;

      & i {
         display: flex;
         align-items: center;
         justify-content: center;
         height: 40px;
         width: 40px;
         background-color: white;
         color: #e88a12;
         border-radius: 1000px;
         font-size: 2rem;

         -webkit-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
         -moz-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
         box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);

         &.pressed {
            background-color: #e88a12;
            color: white;
         }
      }
   }

   & .places-container {
      margin: 2rem 0;
      padding: 0 1rem;
      height: 70%;
      width: 90%;
      border-radius: 10px;
      border: 1px solid rgba(128, 128, 128, 0.288);
      overflow: hidden auto;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
         display: none;
      }
   }
}

@media screen and (min-width: 768px) {
   .search-places-section {
      & .icons-container {
         margin: 1rem 0;

         & i {
            font-size: 3rem;
            height: 60px;
            width: 60px;
            transition: transform 0.2s;

            &:hover {
               transform: scale(1.1);
               cursor: pointer;
            }
         }
      }
   }
}
