.qr-code-page {
   position: absolute;
   left: 0;
   top: 0;
   width: 100vw;
   height: 100vh;
   background-color: #00000050;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;

   & .qr-code-container {
      display: flex;
      flex-direction: column;
      height: 60vh;
      justify-content: space-around;
      align-items: center;
      background-color: white;
      padding: 0 1rem;

      & .qr-code {
         height: 90vw;
         max-height: 500px;
      }

      & button {
         width: 90%;
         border: none;
         background-color: #e88a12;
         color: white;
         font-size: 1.6rem;
         text-transform: uppercase;
         padding: 1rem 0;
         border-radius: 1000px;
         letter-spacing: 2px;
         font-weight: bold;
         outline: none;
         transition: transform .2s;

         &:hover{
            cursor: pointer;
            transform: scale(1.1);
         }
      }
   }

   & p {
      text-align: center;
      font-size: 1.4rem;
      background-color: white;
      margin-bottom: 1rem;
      padding: 1rem 1rem;
   }
}
