.join-request-section {
   height: 100%;
   display: flex;
   align-items: center;
   flex-direction: column;
   justify-content: space-around;

   & .ask-for-request {
      font-size: 1.6rem;
      padding: 1rem 1rem;
      border-radius: 1000px;
      background-color: #e88a12;
      color: white;
      font-weight: bold;
   }

   & .apt-section {
      display: flex;
      flex-direction: column;
      height: 40%;
      justify-content: space-around;
      width: 80%;

      & p {
         font-size: 1.6rem;
         text-align: center;
      }

      & input {
         padding: 1rem 2rem;
         font-size: 1.4rem;
         border-radius: 1000px;
         border: none;
         outline: none;
         border: 1px solid gray;
         text-align: center;
      }

      & button {
         background-color: #e88a12;
         color: white;
         font-size: 1.4rem;
         border-radius: 1000px;
         padding: 1rem 0;
         font-weight: bold;
      }
   }

   & .requested {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.6rem;
      color: gray;
      text-align: center;

      & i {
         background-color: green;
         color: white;
         font-size: 4rem;
         display: flex;
         align-items: center;
         justify-content: center;
         border-radius: 1000px;
         height: 7rem;
         width: 7rem;
      }
   }

   & .accepted {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.6rem;
      text-align: center;
      color: gray;

      & i {
         background-color: green;
         color: white;
         font-size: 4rem;
         display: flex;
         align-items: center;
         justify-content: center;
         border-radius: 1000px;
         height: 7rem;
         width: 7rem;
      }
   }
}

@media screen and (min-width: 800px) {
   .join-request-section {
      & .ask-for-request {
         font-size: 2rem;
         transition: transform .2s;

         &:hover{
            transform: scale(1.1);
            cursor: pointer;
         }
      }

      & .apt-section{
         width: 100%;

         & p {
            font-size: 2rem;
            text-align: center;
            color: gray;
         }

         & input {
            font-size: 2rem;
         }

         & button {
            font-size: 2rem;
            transition: transform .2s;

            &:hover{
               cursor: pointer;
               transform: scale(1.1);
            }
         }
      }
   }
}
