*,
*::after,
*::before {
   margin: 0;
   padding: 0;
   box-sizing: inherit;
}

html {
   box-sizing: border-box;
   font-size: 62.5%;
   max-width: 100vw;
   min-width: 320px;
}
a {
   text-decoration: none;
   color: inherit;
}

body {
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
button,
input,
span,
textarea,
label,
P {
   font-family: "Raleway", sans-serif;
}

.swal2-modal {
   width: 50rem;
}

.swal2-title {
   font-size: 2rem;
}

.swal2-content {
   font-size: 2rem;
}

button {
   border: none;
   outline: none;
}

.section-container {
   background-color: #f6f7f9;
   height: 90vh;
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   overflow: hidden;
}

.section {
   background-color: #fff;
   height: 95%;
   max-width: 1000px;
   width: 90%;
   border-radius: 1rem;
   display: flex;
   flex-direction: column;
   align-items: center;
   overflow: hidden auto;

   -webkit-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
   -moz-box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
   box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.25);
}
