.user-places-section {
   width: 90%;

   & h3 {
      font-size: 1.6rem;
      color: #e88a12;
      padding: 3rem 0;
      border-bottom: 1px solid #e88a12;
      width: 100%;
      text-align: center;
   }

   & .places-list {
      height: 100%;
      width: 100%;
   }

   & .no-places {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      & p {
         font-size: 1.6rem;
         color: gray;
      }
   }
}

@media screen and (min-width: 768px) {
   .user-places-section {
      
      & h3 {
         font-size: 2rem;
      }
   }
}
