.app-icons {
   position: fixed;
   bottom: -35%;
   display: flex;
   justify-content: space-between;
   width: 100%;
   transition: bottom 0.5s;

   & .icon-container {
      display: flex;
      align-items: center;
      background-color: #e88a12;
      width: 8rem;
      height: 8rem;
      border-radius: 1rem;
      overflow: hidden;
      border-radius: 1000px;
      transition: width 0.2s ease;

      &.playstore-icon {
         margin-left: 2vh;
         justify-content: center;
      }
      &.web-icon {
         margin-right: 2vh;
         justify-content: center;
      }

      & i {
         font-size: 4rem;
         color: white;
      }

      & img {
         width: 7vh;
         height: 7vh;
      }

      & p {
         font-size: 2rem;
         color: white;
         text-align: center;
         visibility: hidden;
         position: absolute;
         margin: 0 1rem;
         transition: position 2s;
      }

      &:hover {
         width: 20rem;
         cursor: pointer;

         & p {
            visibility: visible;
            position: static;
         }
      }
   }
}

@media screen and (max-width: 768px) {
   .app-icons {
      & .icon-container {
         &:hover {
            width: 8rem;

            & p {
               visibility: hidden;
               position: absolute;
            }
         }
      }
   }
}
