.nav-container {
   height: 10vh;
   overflow: hidden;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0 2rem;
   border-bottom: 1px solid #cccccc;

   & #mobile-nav {
      position: absolute;
      top: 10vh;
      right: 2%;
      width: 200px;
      height: 100px;
      background-color: white;
      border-radius: 20px;

      -webkit-box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
            -moz-box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
            box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
   }

   & img {
      height: 5vh;
   }

   & .links {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 30%;
      min-width: 420px;
      font-size: 1.6rem;
      font-weight: bold;
      color: gray;

      & *.selected {
         color: #e88a12;
      }

      & *:hover {
         color: #e88a12;
         cursor: pointer;
      }

      & i {
         visibility: hidden;
         font-size: 0;
         position: absolute;
      }

      & .user-photo {
         display: flex;
         align-items: center;
         justify-content: center;
         background-color: #e88a12;
         height: 7vh;
         width: 7vh;
         border-radius: 1000px;

         & p {
            font-size: 2.5rem;
            font-weight: bold;
            color: white;
         }

         &:hover {
            cursor: default;
         }
      }
   }
}

@media screen and (max-width: 800px) {
   .nav-container {
      & img {
         height: 4vh;
      }

      & .photo {
         display: flex;
         align-items: center;
      }

      & .links {
         justify-content: flex-end;
         min-width: 100px;

         & * {
            position: absolute;
            visibility: hidden;
         }

         & i {
            position: static;
            visibility: visible;
            font-size: 2rem;
            margin-right: 1rem;
         }

         & .user-photo {
            position: static;
            visibility: visible;
            height: 5vh;
            width: 5vh;

            & p {
               visibility: visible;
               font-size: 1.4rem;
            }
         }
      }
   }
}
