.forgot-password-page {
   max-height: 100vh;
   height: 100vh;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;

   & h2 {
      font-size: 3vw;
      color: #e88a12;
      text-align: center;
   }

   & img {
      height: 40vh;
   }

   & p {
      color: gray;
      font-size: 1.6rem;
      text-align: center;
   }

   & .custom-form-container {
      width: 80%;
      max-width: 50rem;
      margin: 1rem 0;
   }

   & button {
      font-size: 2vw;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: bold;
      color: white;
      background-color: #e88a12;
      border: none;
      padding: 1rem 2rem;
      border-radius: 10px;
      transition: transform .2s;

      &:hover{
         transform: scale(1.1);
         cursor: pointer;
      }
   }
}

@media screen and (max-width: 800px){
   .forgot-password-page{

      & h2{
         font-size: 8vw;
      }

      & button {
         font-size: 3vw;
      }
   }
}