.sign-in-page {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   padding: 2rem 0;
   max-width: 100vw;
   min-width: 320px;

   & .sign-in-form {
      display: flex;
      flex-direction: column;
      width: 80%;
      align-items: center;
      text-align: center;
      max-width: 100vw;

      & h2 {
         margin: 1rem 0 0 0;
         font-size: 4rem;
         color: #e88a12;
      }

      & form {
         height: 80%;
         width: 100%;
         display: flex;
         flex-direction: column;

         & p {
            font-size: 1.4rem;
            color: gray;
            align-self: flex-end;
            margin: 0.5rem 0 2rem;
         }

         & input[type="submit"] {
            width: 100%;
            border-radius: 10px;
            font-size: 1.6rem;
            align-self: center;
            background-color: #e88a12;
            border: none;
            color: white;
            padding: 1rem 0;
            font-weight: bold;
            text-transform: uppercase;
            letter-spacing: 2px;
            outline: none;
         }

         & .forgot-password {
            align-self: flex-end;
            margin: 2rem 0;
            width: "100%";
            color: gray;
            font-size: 1.4rem;
            text-decoration: underline;
            width: 18rem;
         }
      }

      & .separator {
         display: flex;
         align-items: center;
         justify-content: space-between;
         width: 100%;
         font-size: 1.4rem;
         color: gray;
         text-align: center;
         margin: 0 0 2rem;

         & div {
            height: 1px;
            border-bottom: 1px solid gray;
            width: 20vw;
         }
      }

      & .google-button {
         align-self: center;
         display: flex;
         width: 100%;
         align-items: center;
         justify-content: center;
         font-size: 1.6rem;
         font-weight: bold;
         color: white;
         background-color: #4c8bf5;
         border: none;
         padding: 0.5rem 0;
         border-radius: 10px;
         outline: none;

         & i {
            font-size: 2rem;
            margin-right: 1rem;
            color: #4c8bf5;
            background-color: white;
            padding: 0.5rem 0.5rem;
            border-radius: 10px;
         }
      }
   }

   & .sign-up-redirect {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      width: 80%;
      max-width: 100vw;

      & .img {
         visibility: hidden;
         height: 0;
         margin: 0;
      }

      & h2 {
         color: #e88a12;
         font-size: 1.4rem;
         margin: 1rem 0;
      }

      & .sign-up-button {
         background-color: #e88a12;
         color: #fff;

         font-size: 1.6rem;
         margin: 1rem 0 0;
         font-weight: bold;
         letter-spacing: 2px;
         padding: 1rem 0;
         width: 100%;
         border-radius: 10px;
         text-transform: uppercase;
      }
   }
}

@media screen and (min-width: 800px) {
   .sign-in-page {
      max-height: 100vh;
      height: 100vh;
      width: 100vw;
      background-image: url(../../../img/sign-in-background.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      display: grid;
      grid-template-columns: 50% 50%;

      & .sign-up-redirect {
         width: 100%;
         grid-column: 1;
         grid-row: 1;

         & h2 {
            font-size: 4rem;
            color: white;
         }

         & .img {
            visibility: visible;
            height: 30rem;
            margin: 5vh 0;
         }

         & .sign-up-button {
            background-color: white;
            width: 20vw;
            min-width: 250px;
            color: #e88a12;
            font-size: 1.8rem;
            transition-property: transform, box-shadow;
            transition-duration: 0.2s;

            &:hover {
               cursor: pointer;
               transform: scale(1.1);
               -webkit-box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
               -moz-box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
               box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
            }
         }
      }

      & .sign-in-form {
         margin-left: 5vw;
         width: 60%;

         & form {
            & p {
               margin: 3rem 0;

               &:hover {
                  cursor: pointer;
               }
            }

            & input[type="submit"] {
               width: 20vw;
               min-width: 250px;
               font-size: 1.8rem;
               transition-property: transform;
               transition-duration: 0.2s;

               &:hover {
                  transform: scale(1.1);
                  cursor: pointer;
               }
            }
         }
         & .separator {
            margin: 2rem 0;
            
            & div {
               width: 10vw;
            }
         }

         & .google-button {
            width: 20vw;
            min-width: 250px;
            font-weight: bold;
            transition: transform 0.2s;

            &:hover {
               cursor: pointer;
               transform: scale(1.1);
            }

            & i {
               font-size: 3rem;
            }
         }
      }
   }
}
