.trans-loader {
   position: fixed;
   top: 0%;
   left: 0%;
   width: 100vw;
   height: 100vh;
   background-color: #00000079;
   display: flex;
   align-items: center;
   justify-content: center;

   .lds-facebook {
      display: flex;
      position: relative;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;

      & div {
         display: inline-block;
         width: 50px;
         margin: 0 1rem;
         background: #fff;
         border: 3px solid #e88a12;
         animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
      }
      & div:nth-child(1) {
         left: 8px;
         animation-delay: -0.24s;
      }
      & div:nth-child(2) {
         left: 32px;
         animation-delay: -0.12s;
      }
      & div:nth-child(3) {
         left: 56px;
         animation-delay: 0;
      }
   }
}

@keyframes lds-facebook {
   0% {
      top: 8px;
      height: 100px;
   }
   50%,
   100% {
      top: 24px;
      height: 60px;
   }
}
