.place-info {
   scrollbar-width: none;
   -ms-overflow-style: none;
   height: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 90%;

   &::-webkit-scrollbar {
      display: none;
   }

   & img {
      margin: 1rem auto;
      height: 17vh;
      width: 17vh;
      border-radius: 1000px;

      -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
      -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
   }

   & .title {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 2rem;
      color: #e88a12;
      margin: 1rem 0;
      text-align: center;
      width: 90%;

      & i {
         display: flex;
         justify-content: center;
         align-items: center;
         min-height: 40px;
         min-width: 40px;
         color: white;
         background-color: #e88a12;
         font-size: 2.5rem;
         border-radius: 1000px;
      }
   }
}
