.home-page {
   max-width: 100vw;
   min-width: 320px;
   background-image: url(../../../img/ciudad.jpg);
   background-size: cover;
   background-position: center center;
   background-repeat: no-repeat;
   background-attachment: fixed;
   overflow-x: hidden;

   & header {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      min-height: 100vh;
      background: rgb(232, 138, 18);
      background: linear-gradient(
         180deg,
         rgba(232, 138, 18, 0.95) 40%,
         rgba(252, 223, 186, 0.95) 100%
      );

      & p {
         color: white;
         font-size: 3em;
      }

      & .home-logo {
         width: 60vw;
         transition: width 0.1s;
      }

      & .header-buttons {
         margin-top: 10rem;
         width: 80vw;
         display: flex;
         justify-content: space-around;

         & .header-button {
            width: 312px;
            height: 70px;
            border-radius: 100rem;
            overflow: hidden;
            display: flex;
            justify-content: space-around;
            align-items: center;
            transition-property: background-color, transform, color;
            transition-duration: 0.2s;
            letter-spacing: 2px;
            font-weight: bold;
            text-transform: uppercase;
            font-size: 2em;
            color: white;

            &:hover {
               transform: scale(1.1);
            }

            & i {
               font-size: 4rem;
               transition: color 0.2s;
            }
         }

         & .play-store-button {
            background-color: #e88a12df;

            & i {
               color: white;
            }

            &:hover {
               background-color: #ffffffdf;
               color: #e88a12;

               & i {
                  color: #e88a12;
               }
            }
         }

         & .web-button {
            border: 1px solid #e88a12;
            background-color: #ffffffdf;
            color: #e88a12;

            & i {
               color: #e88a12;
            }

            &:hover {
               background-color: #e88a12df;
               color: #ffffffdf;

               & i {
                  color: #ffffffdf;
               }
            }
         }
      }
   }

   & .functions-section {
      height: 100vh;
      background-color: white;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 20% 75% 5%;
      align-items: center;

      & h2 {
         font-size: 4rem;
         text-align: center;
         color: #e88a12;
      }

      & .function-description {
         font-size: 1.6rem;
         color: gray;
         text-align: center;
      }

      & .functions-container {
         display: grid;
         grid-template-columns: 33% 33% 33%;

         & .function {
            height: 100%;
            min-height: 50vh;
            width: 90%;
            justify-self: center;
            border-radius: 1rem;
            -webkit-box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
            -moz-box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);
            box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.49);

            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 10% 20% 50%;
            align-items: center;
            text-align: center;

            & .function-icons {
               margin-top: -5rem;
               display: flex;
               align-items: center;
               justify-content: center;
               align-content: center;

               & i {
                  background-color: #e88a12;
                  font-size: 5rem;
                  color: white;
                  width: 8.5rem;
                  padding: 2.2rem 0;
                  border-radius: 100rem;
                  margin: 0 1%;
               }
            }

            & h3 {
               color: #e88a12;
               font-size: 3.3vh;
               margin: 1rem 0;
               letter-spacing: 2px;
            }

            & ul {
               height: 100%;
               padding: 3rem;
               text-align: justify;

               & li {
                  margin: 1rem 0;
               }

               & p {
                  font-size: 1.7rem;
                  color: gray;
                  line-height: 1;
               }
            }
         }
      }
   }

   & .form-section {
      height: 100vh;
      background-color: #ffffff;
      padding: 2rem 0;

      & h2 {
         font-size: 4rem;
         color: #e88a12;
         text-align: center;
      }

      & .form-container {
         display: grid;
         height: 90%;
         grid-template-columns: 40% 60%;
         align-items: center;
         justify-content: center;

         & .form-text {
            text-align: right;
            font-size: 4rem;
            font-style: italic;

            & p {
               color: #e88a12;
            }

            & :last-child {
               font-size: 2rem;
               color: gray;
            }
         }

         & form {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-self: left;
            margin-left: 3rem;
            width: 80%;

            & label {
               align-self: flex-start;
               font-size: 2rem;
               color: #e88a12;
            }

            & input {
               width: 100%;
               padding: 1rem 1rem;
               font-size: 2.2rem;
               color: gray;
               margin: 1rem 0;
               border: none;
               border-bottom: 2px solid gray;
            }

            & textarea {
               width: 100%;
               border: none;
               resize: none;
               border-bottom: 2px solid gray;
               padding: 1rem 1rem;
               color: gray;
               font-size: 2rem;
            }

            & input[type="submit"] {
               width: 50%;
               align-self: flex-start;
               background-color: #e88a12;
               color: white;
               border: none;
               transition: transform 0.2s;

               &:hover {
                  cursor: pointer;
                  background-color: #e88a01;
                  transform: scale(1.1);
               }
            }
         }

         & .form-separator {
            height: 90%;
            border: 1px solid gray;
            width: 1%;
            margin: 0 auto;
            align-self: center;
         }
      }
   }
}

@media screen and (max-width: 800px) {
   .home-page {
      & header {
         text-align: center;
         & .home-logo {
            width: 90vw;
            margin: 1rem 0;
            transition: width 0.1s;
         }

         & p {
            font-size: 2em;
         }

         & .header-buttons {
            flex-direction: column-reverse;
            align-items: center;

            & .header-button {
               width: 80vw;
               margin: 2rem 0;
               justify-content: center;

               & i {
                  margin-right: 1rem;
               }
            }
         }
      }

      & .functions-section {
         height: auto;
         grid-template-rows: 10% 85% 5%;
         padding: 2rem 0;

         & .functions-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 5rem 0;

            & .function {
               margin: 3rem 3rem;
            }
         }
      }

      & .form-section {
         padding: 10rem 0;
         height: auto;
         & .form-container {
            grid-template-columns: 1fr;
            grid-template-rows: 40% 60%;

            & .form-text {
               text-align: center;
               margin: 10rem 0;
            }
         }
      }
   }
}
